import React, { useState, useEffect, useContext } from 'react'
import { Dialog } from '@material-ui/core'
import '../../App.css'
import ChartsPage from './chart'
import { response, logins,customer } from '../../App'
import { toast } from 'react-toastify'
import axios from 'axios'
import PuffLoader from "react-spinners/PuffLoader";
import { css } from "@emotion/react";

const override = css`
  display: block;
  margin: 0 auto;
  border-color: red;
`;

export const Dashboard = () => {
    const [data, setData] = useContext(response);
    useEffect(() => {
        if (data === null && sessionStorage.getItem("dat1") === null) {
            setLoading(true)
            setopen(true)
        }
        else{
            setLoading(false)
            setopen(false)
        }
        if(sessionStorage.getItem("user")!==null){
            setLoggedInUser(sessionStorage.getItem("user"))
        }
    },[data])
    const [isloggedin, setisloggedin] = useContext(logins)
    const [loggedInUser,setLoggedInUser] = useContext(customer)
    const [invoiceList, setInvoiceList] = useState([])
    const [ref, setRef] = useState("")
    const [open, setopen] = useState(false)
    const openModal = () => setopen(true)
    const close = () => {
        setopen(false)
    }
    const [loading, setLoading] = useState(false)
    const [fromValue, sfromvalue] = useState("")
    const [toValue, stovalue] = useState("")
    const logout = () => {
        sessionStorage.clear()
        setisloggedin(false)
        toast.info("Logged Out!")
    }
    const createInvoice = (webfile) => {
        const obj = {
            transRef: webfile.reference,
            mobile: webfile.mobile,
            email: webfile.email
        }
        setLoading(true)
        axios.post(`/coj/api/v1/invoicecert`, obj, { headers: { Authorization: `Bearer ${sessionStorage.getItem('uux')}` } })
            .then(r => {
                setLoading(false)
                toast.success("Certificate created!")
            })
            .catch(e => {
                setLoading(false)
                toast.error("certificate creation failed!")
            })
    }
    const searchfunc = () => {
        setData(null)
        setLoading(true)
        setopen(true)
        axios.get(`/coj/api/v1/user/getdata`, { headers: { Authorization: `Bearer ${sessionStorage.getItem('uux')}` }, params: { from: new Date(fromValue.substring(0, 4), (fromValue.substring(5, 7) - 1), fromValue.substring(8, 10)).getTime(), to: new Date(toValue.substring(0, 4), toValue.substring(5, 7) - 1, toValue.substring(8, 10)).getTime() } })
            .then(rq => {
                setLoading(false)
                setopen(false)
                setData(rq.data)
            })
            .catch(e => {
                setopen(false)
                setLoading(false)
                toast.error("Error fetching data!")
            })
    }
    const searchInvoice = () => {
        setLoading(true)
        axios.get(`/coj/api/v1/webfile/search/ref/mobile/${ref}`, { headers: { Authorization: `Bearer ${sessionStorage.getItem('uux')}` } })
            .then(r => {
                setLoading(false)
                setInvoiceList(r.data)
            })
            .catch(e => {
                setLoading(false)
                toast.error("Error fetching data")
            })
    }
    const reset = () => {
        setLoading(true)
        setopen(true)
        setData(null)
        setTimeout(()=>{
            setData(JSON.parse(sessionStorage.getItem("dat1")))
            setLoading(false)
            setopen(false)
        },400)
       
    }

    return (
        <div className="dashboard">
            From 
            <input classname="date-input" type="date" value={fromValue} onChange={e => sfromvalue(e.target.value)} />
            To <input classname="date-input" type="date" value={toValue} onChange={e => stovalue(e.target.value)} />
            <span className="btn-search" onClick={searchfunc}>filter </span>
            <span className="btn-reset" onClick={reset}>clear </span>
            <span className="btn-reset" onClick={openModal}>Create Certificate </span>
            {/* <span className="logout" onClick={logout}>Logout</span> */}
            <div className="username">
            {data!==null&&<span>{loggedInUser}</span>}<img src="https://img.icons8.com/external-kmg-design-glyph-kmg-design/200/000000/external-power-user-interface-kmg-design-glyph-kmg-design.png" className='img-logout' alt="logout" onClick={logout}/>
            </div>
           
            <fieldset>
                <legend>Data Summary</legend>
                <table>
                    <th>
                        Web Files
                    </th>
                    <th>
                        Pending Email Request
                    </th>
                    <th>
                        Pending SMS Request
                    </th>
                    <th>
                        Verified Files
                    </th>
                    <tr className="admin-tablerow">
                        <td>
                            {data !== null ? data.webfilecount : ""}
                        </td>
                        <td>
                            {data !== null ? data.pendingemailrequestcount : ""}
                        </td>
                        <td>
                            {data !== null ? data.pendingsmsrequestcount : ""}
                        </td>
                        <td>
                            {data !== null ? data.verifiedfiles : ""}
                        </td>
                    </tr>
                </table>
            </fieldset>
            {data !== null && <div className="chart">
                <ChartsPage data1={data.total - data.webfilecount} data2={data.webfilecount} label1={"Total"} label2={"Webfile"} desc={"Webile"} />
                <ChartsPage data1={data.total - data.pendingemailrequestcount} data2={data.pendingemailrequestcount} label1={"Sent"} label2={"Pending"} desc={"Pending Email Requests"} />
                <ChartsPage data1={data.total - data.pendingsmsrequestcount} data2={data.pendingsmsrequestcount} label1={"Sent"} label2={"Pending "} desc={"Pending SMS Requests"} />
                <ChartsPage data1={data.total - data.verifiedfiles} data2={data.verifiedfiles} label1={"Total"} label2={"unverified Files"} desc={"Verified Files"} />
            </div>}
            <br />
            <Dialog
                scroll="body"
                maxWidth="lg"
                open={open}
                onClose={close}
            >
                <div className="modal">
                    {loading ? <PuffLoader loading={true} size={90} css={override} color={"#FDB45C"} speedMultiplier={4} /> : <center>
                        <h1>Invoice Certificate</h1>

                        <input className="mobile-input" type="email" required placeholder="reference or mobile" value={ref} onChange={e => setRef(e.target.value)} /><br /><button className='btn-search' onClick={searchInvoice}>search</button><br />
                        {invoiceList.length !== 0 && <table className='table-admin'>
                            <th>
                                S/N
                            </th>
                            <th>
                                Reference
                            </th>
                            <th>
                                Bill Reference
                            </th>
                            <th>
                                Mobile
                            </th>
                            <th>
                                Date
                            </th>
                            <th>
                                Action
                            </th>
                            {invoiceList[0] !== undefined && invoiceList.map((p, i) => <tr>
                                <td>
                                    {i + 1}
                                </td>
                                <td>
                                    {p.reference}
                                </td>
                                <td>
                                    {p.billRef}
                                </td>
                                <td>
                                    {p.mobile}
                                </td>
                                <td>
                                    {new Date(p.created).toLocaleDateString()}
                                </td>
                                <td>
                                    {<button className='btn-admin' onClick={() => createInvoice(p)}>Create Invoice</button>}
                                </td>
                            </tr>)}

                        </table>
                        }
                    </center>}
                </div>
            </Dialog>
        </div>
    )
}
