import React, { useState, useContext } from 'react'
import '../../App.css'
import axios from 'axios'
import { customer, logins,response } from '../../App'
import { toast } from 'react-toastify'
export const Login = () => {
const [isLoggedIn,setIsLoggedIn]=useContext(logins)
const [respData, setRespData] = useContext(response)

const [loggedInUser,setsetLoggedInUser] = useContext(customer)
const [user, setUser] = useState({
    username:"",
    password:""
})

  const login = () => {
    axios.post("/coj/api/v1/user/login",user)
    .then(r=>{
      setIsLoggedIn(true)
      sessionStorage.setItem("uux",r.data.jwt)
      setsetLoggedInUser(r.data.user)
      sessionStorage.setItem("user",r.data.user)
      axios.get("/coj/api/v1/user/getdata",{  headers: { Authorization: `Bearer ${sessionStorage.getItem('uux')}` } })
      .then(rq=>{
        setRespData(rq.data)
        sessionStorage.setItem("dat1",JSON.stringify(rq.data))
      })
      .catch(e=>{
        toast.error("Error loading data")
        setRespData({
          webfilecount:0,
          verifiedfiles:0,
          pendingsmsrequestcount:0,
          pendingemailrequestcount:0,
          total:0
        })
      })
    })   
    .catch(e=>toast.error("Invalid credentials")) 

  }
 
    return (
        <div className="login">
            <fieldset id="login-fieldset">
                <legend>Admin Login</legend>
                <input id="login-input" type="text" placeholder="username" value={user.username} onChange={e=>setUser({...user,username:e.target.value})}/><br/>
                <input id="login-input"type="password" placeholder="password" value={user.password} onChange={e=>setUser({...user,password:e.target.value})}/>
            <br/>
            <br/>
            <span className="login-button" onClick={login}>Login</span>
            </fieldset>
            
            
        </div>
    )
}
